// reset scss features,, normalizing css settings for all browswer
@import 'scss-reset/_reset.scss';
// *modules: 변수, mixin 패키지 

@import 'modules';
@import './layout/board';

// ! 뭔가 안되네용 ㅠㅠ 😥 
// @include font-face('NotoSansCJKkr-Regular', 'NotoSansCJKkr-Regular', 400);
// $font-stack: NotoSans, sans-serif;

::-webkit-scrollbar{
  width: 0;
  height: 0;
}
html{
  font-size: 10px; // rem 기준
}
// * normalize
body{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: 18px;
  font-style: normal;
  // font: 100% $font-stack;

  letter-spacing: -0.5px;
  color: #2E2E2E;

  line-height: 1;
  word-break: keep-all;

  min-width: 1440px;
  a{
    text-decoration: none;
    outline: none;
    color:inherit;
    &:hover, &:active{
      text-decoration: none; 
      background-color:transparent;
    }
  }
  span, i{
    display: inline-block;
  }
}

// *🍚️ theme
.App{
  position: relative;
  .width1440{ 
    width: $width_1440;
    margin: 0 auto;
  }    
  // * global 지수빈
  .clear::after{
    clear: both;
    display: block;
    content: '';
  }
  .wrap-img{
    img{
      display: block;
      width: 100%;
      min-height: 100%;
    }
  }
  .date{
    color: #B0B0B0;
    font-size: 15px;
  }
  // * global 정수진C 
  // 더보기, 전체보기같은 버튼
  .btn-link{
    display: inline-block;
    color: $color-2C99FF_azure;
    font-size: 1.8rem;
    font-weight: 500;
    padding: 1em;
  
    text-align: center;
  }
}


// ! layout모두 잡고 한번에 작성하기
// ! 🎈illust
.illust-main-customer{
  position: absolute;
  bottom: 0;
  left: 134px;
  width: 289px;
  height: 357px;

  background-color: red;
}
.illust-subpage{
  position: absolute;
  bottom: 0;
  right: 0;  
  width: 829px;
  height: 380px;
  background-color: blue;
  opacity: 0.2;
}

// 💫️ icons
.logo{
  display: block;
  width: 140px;
  height: 100%;
  // font-size: 0;
  background-image: url('../../public/asset/images/icon_logo-blue.png');
  background-repeat: no-repeat;
  background-position: center;
}
.icon{
  background-color: purple;
}
.icon-link{
  width: 15px;
  height: 15px;
}
